export default class App extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      problem_sequence : [...problem_sequence],
      problem_number : 0,
      repetition : 1
    }
    window.goToWord = (word) =>{
      this.setState({
        problem_number :
               problem_sequence.indexOf(word.toUpperCase()) || 0,
        repetition : 1
      })
    }
  }

  render = () =>{
  let word = this.state.problem_sequence[this.state.problem_number]
  console.log(word)
  return (
    <View style={styles.container}>
      {<ISProblem
        onDone={(force_next=false)=>{
          console.log(force_next, this.state.repetition, NUM_REPS)
          if(!force_next && this.state.repetition < NUM_REPS){

            this.setState({repetition: this.state.repetition+1})
          }else{
            this.setState({
             problem_number: this.state.problem_number+1,
             repetition:1
            })
          }
        }} 
        word={word}
        repetition={this.state.repetition}
        grapheme_options = {grapheme_options[word]}
      />}
      {/*<Test style={{height: "100%"}}/>*/}
      
    </View>
  );
  }
}

