const default_anim_config = {
    speed: 20,
    strength: 0.05,
    useNativeDriver: false,
}

function gen_shadow(elevation){
  if(Platform.OS == 'android'){
    return {elevation : elevation}
  }else{
    return {shadowRadius: elevation * .627,
    shadowOpacity: 0.20 + elevation * 0.015,
    shadowOffset : {
      width : elevation /3,
      height: elevation /2,
      }
    }
  }
}


