      {<Animated.View style={[styles.grapheme,
        is_vowel && {backgroundColor : '#ffc0c0'},
        {position :'absolute', zIndex: -1,
        // left :-10, 
        // top :-10, 
         width:100,height:100},
        {transform : [
          // {scaleX: 100.0 / this.state.size_anim.x},
          // {scaleY: 100.0 / this.state.size_anim.y},
          {scaleX: this.state.size_anim.x},
          {scaleY: this.state.size_anim.y},
        ]},
        gen_shadow(this.state.elevation)

      ]}/>}


      <Animated.Text selectable={false} style={[styles.grapheme_text,{color: color, fontSize:fontSize}]}>{g}</Animated.Text>
