  updateTiles = (force=false) =>{
    let updates = []
    // console.log("UPDATE TILES")
    for(let tile of this.state.all_tiles || []){
      if(tile.needs_update || force){
        // console.log((tile.unplaced_ref.current && 1) || 0, (tile.placed_ref.current && 1) || 0,)
        let plh_ref = tile.placed_ref.current || tile.unplaced_ref.current
        // console.log(plh_ref, tile.tile_ref)

        if(tile.is_removed){console.log("removed")}
        let r = tile.tile_ref.current
        if(r && (plh_ref || tile.is_removed)){
            let u = r.update(plh_ref)
            updates.push(u)
            // u.then(()=>{console.log("DONE")})
            // console.log(u)
        }
      }
    }
    // console.log(updates)
    return Promise.all(updates)
  }


