import { Audio } from 'expo-av';
import {words_dict, phones_dict, letter_name_dict, prompt_dict} from '../assets/sounds';

function timeout(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
}

async function playToEnd(sound_file){
    let sound = (await Audio.Sound.createAsync(sound_file)).sound;
    return new Promise(resolve =>{
        sound.setOnPlaybackStatusUpdate((status)=>{
            if(status.didJustFinish){resolve()}
        })
        sound.playAsync()
    } )
}

var playing = false;

export function handleAudio(arr,init=false){
    let promise = new Promise(async (resolve,reject) =>{
        //If it's not an array make it an array
        if(!Array.isArray(arr)){arr = [arr]}
    	
        for(let item of arr){
            console.log("ITEM",item)
            if(isNaN(parseInt(item))){
                if(typeof item === 'string'){
                    //See if is in phones_dict
                    let sound_file = phones_dict[item] || null

                    //See if is in letter_sounds_dict (must preceded w/ '#' like "#A")
                    if(!sound_file && item[0] === "#"){
                        sound_file = letter_name_dict[item.substring(1).toUpperCase()] || null
                    }
                    //See if is in words_dict
                    if(!sound_file){sound_file = words_dict[item.toLowerCase()] || null}

                    //See if is in prompt_dict
                    if(!sound_file){sound_file = prompt_dict[item.toLowerCase()] || null}

                    //Error
                    if(!sound_file){
                        alert("NO SOUND: ", item);
                        reject()
                        return;
                    }
                    await playToEnd(sound_file)
                } 
                if(typeof item === 'string'){
                    console.log("PROMBLEM");
                }
            }else{
                // Wait 'item' milliseconds
                await timeout(item)
            }
        }

        resolve()
    })
    return promise
}

export function playAudioFromText(x) {
    handleAudio(x.split(/[\t ,]+/), true);
} 

function pauseAudio() { 
    x.pause(); 
} 

