import {default_phonemes, default_letters	,grapheme_dict, word_phonemes, phoneme_types} from "../assets/data_dicts"

export let a = 1
class Biphoneme {
	constructor(p1,p2=""){
		p1 = p1.replace(/[0-9]/g, ''); //Get rid of digits
		p2 = p2.replace(/[0-9]/g, ''); //Get rid of digits
		this.phonemes = [p1,p2];
		this.compound = p2 !== "";
	}
	toString(){
		if(this.compound){
			return this.phonemes.join(",")
		}else{
			return this.phonemes[0]
		}
		// return "Biphoneme(" + this.phonemes.toString() + ")";
	}
}

class SpellingCorrection{
	constructor(partial_spelling,
				insert_index,
				insert_value="",
				is_vowel=false,
				remove_length=0,
				insert_phoneme=null){
		this.partial_spelling = partial_spelling
		this.insert_index = insert_index
		this.insert_value = insert_value
		// this.remove_index = remove_index
		this.remove_length = remove_length

		this.type = remove_length == 0 ? "insert" : (insert_value != "" ? "replace" : "remove") 
		this.is_vowel = is_vowel//["A","E","I","O","U"].includes(insert_value)
		this.is_correct = insert_index == -1//["A","E","I","O","U"].includes(insert_value)
		this.insert_phoneme = insert_phoneme
	}

	copy(){
		return new SpellingCorrection(
			this.partial_spelling,
			this.insert_index,
			this.insert_value,
			this.is_vowel,
			this.remove_length,
			this.insert_phoneme,
		)
	}


	toString(){
		if(this.is_correct){
			return 'correct'
		}else if(this.type == "insert"){
			return 'insert('+this.insert_index+","+this.insert_value +")"
		}else if(this.type == 'replace'){
			return 'replace('+this.insert_index+","+this.remove_length+","+this.insert_value +")"
		}else{
			return 'remove('+this.insert_index+","+this.remove_length+")"
		}
	}
}

export function isVowel(l){
	// console.log(l,["A","E","I","O","U"].includes(l.toUpperCase()))
	return ["A","E","I","O","U"].includes(l.toUpperCase())
}

export function next_correction(word, spelling){
	let is_vowel = false
	let ps = best_partial_spelling(word, spelling, true)
	if(ps.remaining_letters.length == 0 && ps.remaining_phonemes.length == 0){
		ps = best_partial_spelling(word, spelling, false)
		if(ps.remaining_letters.length == 0 && ps.remaining_phonemes.length == 0){
			return new SpellingCorrection(ps,-1)
		}
		is_vowel = true
	}

	let rl = ps.remaining_letters.length
	if(rl > 0 && ps.remaining_phonemes.length == 0){
		return new SpellingCorrection(ps,spelling.length-rl,"",is_vowel,rl) 
	}
	
	//TODO: should make it use the true letter instead of default
	// let phonetic_spelling = word_phonemes[word.toUpperCase()]
	let gr = default_letters[ps.remaining_phonemes[0]].toUpperCase()
	// console.log("gr", gr)
	// console.log(is_vowel && isVowel(ps.remaining_letters[0]))
	if(rl == 0 || (is_vowel && isVowel(gr)) ){
		return new SpellingCorrection(ps,spelling.length-rl,gr,is_vowel) 
	}else{
		return new SpellingCorrection(ps,spelling.length-rl,gr,is_vowel,1) 
	}
}

