//Applies modified version of Levenshtein distance to find best sequence
// of corrections
export function best_correction_sequence(word,spelling, prioritize_consonants=false, return_break_down=false){
	// console.log(word,spelling)
	if(Array.isArray(word)){word = word.join("")}
	if(Array.isArray(spelling)){spelling = spelling.join("")}
    var a = spelling, m = [], e=[], i, j, min = Math.min;

    let phonetic_spelling = word_phonemes[word.toUpperCase()]
    let b = [...phonetic_spelling]

    let w_bd = (new PartialSpelling(word.toUpperCase(),phonetic_spelling,[],[])).find_best_break_down()
    // let w_ps = best_partial_spelling(word,word)


    // console.log("w_bd", w_bd)

    for (i = 0; i <= a.length; i++){
    	m.push([])
    	e.push([])
    }

    //Init matrices
    let start_ps = new PartialSpelling(spelling.toUpperCase(),phonetic_spelling,[],[])
    let ps = start_ps
    m[0][0] = [ps]
    e[0][0] = 0
    for (i = 1; i <= a.length; i++){
    	ps = ps.delete(w_bd)
    	m[i][0] = [ps]
    	e[i][0] = i
    }
    ps = start_ps
    for (j = 1; j <= b.length; j++){
    	// console.log(j)
    	ps = ps.insert(w_bd)
    	m[0][j] = [ps]
    	e[0][j] = j
    }
    for(var i = 1; i <= a.length; i++){
	    for(var j = 1; j <= b.length; j++){
	    	m[i][j] = []
	    	e[i][j] = null
		}
	}
    // console.log(m)
    // console.log(e)

    for(var i = 1; i <= a.length; i++){
	    for(var j = 1; j <= b.length; j++){

	    	// let matches = {}
	    	
		    for(let ps of m[i-1][j-1]){
		    	let n_l = ps.committed_letters.length
	    		let n_p = ps.committed_phones.length
		    	// console.log("ps",ps)
		    	let match_set = ps.all_next_matching_ps()
		    	for (let match of match_set){
		    		// console.log(match)
		    		let d_l = match.committed_letters.length-n_l
		    		let d_p = match.committed_phones.length-n_p
		    		// console.log(d_l,d_p)
		    		m[i+d_l-1][j+d_p-1].push(match)
		    	}
		   	}	
		    
		    // matches = [...(m[i][j] || []),...matches]
			if(m[i][j].length > 0){
				// console.log("!",i,j)
				// m[i][j] = matches; // match
				e[i][j] = e[i-1][j-1]; // match
			}else{
				let subst = e[i-1][j-1]
				let inser = e[i][j-1]
				let delet = e[i-1][j]
				let min = Math.min(subst, inser, delet)
				// console.log(i,j)
				// console.log(min, subst, inser, delet)
				// console.log(w_ps)
				if(subst == min){ //substitute
					m[i][j] = m[i-1][j-1].map((ps)=>ps.substitute(w_bd))
					e[i][j] = subst + .99// - .5 && isVowel(w_ps.committed_letters[j] || "")||0
				}else if(inser == min){ //insert
					// let nxt_g = w_ps.committed_letters[m[i][j-1][0].committed_phones.length] || ""
					m[i][j] = m[i][j-1].map((ps)=>ps.insert(w_bd))
					e[i][j] = inser + 1 //- .5 && isVowel(w_ps.committed_letters[j] || "")||0
				}else{ //delete
					m[i][j] = m[i-1][j].map((ps)=>ps.delete(w_bd))
					e[i][j] = delet + 1
					

					
				}
		    }
		}
	}

	ps = m[a.length][b.length][0]
	
	// console.log(word, spelling)
	
	let bd = ps.extract_break_down(true)
	// console.log("BREAK DOWN:", spelling, bd)
	
	let corr_seq = []
	while(ps.parent != null){
		if(ps.correction != null){
			corr_seq.splice(0,0,ps.correction)
		}
		// console.log(ps)
		ps = ps.parent
	}



	// console.log(corr_seq.map((x)=>x.toString()))
	if(prioritize_consonants){
		corr_seq = favor_consonant_corrections(corr_seq)
	}
	// console.log(corr_seq.map((x)=>x.toString()))
	if(corr_seq.length == 0){corr_seq = [new SpellingCorrection(m[a.length][b.length][0],-1)]}

	if(return_break_down){
		return [corr_seq, bd]//m[a.length][b.length][0].extract_break_down(true)]
	}else{
		return corr_seq
	}
	
	
}

function best_partial_spelling(word, spelling, allowSkipInteriorVowels=false){
	if(Array.isArray(word)){word = word.join("")}
	if(Array.isArray(spelling)){spelling = spelling.join("")}
	let phonetic_spelling = word_phonemes[word.toUpperCase()]
	let ps = new PartialSpelling(spelling.toUpperCase(),phonetic_spelling,[],[])
	// console.log(String(ps))
	let leafs = ps.resolve(allowSkipInteriorVowels)
	leafs = leafs.sort((a,b) => b.committed_letters.length - a.committed_letters.length)

	// console.log(spelling, String(leafs[0]))

	return leafs[0]
	// console.log(spelling, "LEAFS",leafs)
	
}

export function best_break_down(word, spelling){
	if(Array.isArray(word)){word = word.join("")}
	if(Array.isArray(spelling)){spelling = spelling.join("")}
	let phonetic_spelling = word_phonemes[word.toUpperCase()]
	let ps = new PartialSpelling(spelling.toUpperCase(),phonetic_spelling,[],[])
	return ps.find_best_break_down()
}

//A structure that keeps track of all intermediate phonetic interpretations of
// an input spelling. 
class PartialSpelling {
	constructor(remaining_letters,
				remaining_phonemes,
				committed_letters,
				committed_phones,
				cum_error=0){
		this.remaining_letters = [...remaining_letters];
		this.remaining_phonemes = remaining_phonemes.map((x) => x.replace(/[0-9]/g, ''));
		this.committed_letters = [...committed_letters]
		this.committed_phones = [...committed_phones];
		this.present_phones = []
		this.cum_error = cum_error
		this.parent = null
		this.correction = null
		// this.true_remaining_letters =[...true_remaining_letters]
		// console.log("INSTANCE:");
		// console.log(this.remaining_letters);
		// console.log(this.remaining_phonemes);
		// console.log(this.committed_letters);
		// console.log(this.committed_phones);
	}

	substitute(w_bd){
		let nxt_g = w_bd[this.committed_phones.length][1][0] || ""
		let rl = [...this.remaining_letters]; rl.splice(0,1)
		let rp = [...this.remaining_phonemes]; rp.splice(0,1)
		let cl = [...this.committed_letters, nxt_g]
		let cp = [...this.committed_phones,this.remaining_phonemes[0]]
		let ps = new PartialSpelling(rl,rp, cl, cp, this.cum_error+1)
		ps.present_phones = [...this.present_phones,default_phonemes[this.remaining_letters[0]]]
		// console.log("SUB",ps.present_phones)
		ps.parent = this
		ps.correction = new SpellingCorrection(this, cl.length-1,nxt_g, isVowel(nxt_g),1) 
		ps.correction.insert_phoneme = this.remaining_phonemes[0]

		return ps
		

	}

	insert(w_bd,w_error=1){
		let nxt_g = w_bd[this.committed_phones.length][1][0] || ""
		// console.log("nxt_g",nxt_g)
		let rl = [...this.remaining_letters]//; rl.splice(0,1)
		let rp = [...this.remaining_phonemes]; rp.splice(0,1);
		let cl = [...this.committed_letters, nxt_g]
		let cp = [...this.committed_phones, this.remaining_phonemes[0]]
		let ps = new PartialSpelling(rl, rp, cl, cp,this.cum_error+w_error)
		ps.present_phones = [...this.present_phones]
		ps.parent = this
		ps.correction = new SpellingCorrection(this, cl.length-1,nxt_g, isVowel(nxt_g)) 
		ps.correction.insert_phoneme = this.remaining_phonemes[0]
		// console.log("IIIII:",ps.correction.insert_phoneme)
		// console.log(ps.correction.toString())
		return ps
		
	}

	delete(){
		let rl = [...this.remaining_letters]; rl.splice(0,1)
		let rp = [...this.remaining_phonemes]
		let cl = [...this.committed_letters]
		let cp = [...this.committed_phones]
		let ps = new PartialSpelling(rl, rp, cl, cp,this.cum_error+1)
		ps.present_phones = [...this.present_phones,default_phonemes[this.remaining_letters[0]]]
		// console.log(this.committed_letters,this.remaining_letters)
		ps.parent = this
		ps.correction = new SpellingCorrection(this, this.committed_letters.length,'', isVowel(this.remaining_letters[0]||""),1) 
		return ps
	}

	next_ps(n_l, n_p){
		let cl = [...this.committed_letters]
		cl.push(...this.remaining_letters.slice(0,n_l))
		let cp = [...this.committed_phones]
		cp.push(...this.remaining_phonemes.slice(0,n_p))

		let ps = new PartialSpelling(
			this.remaining_letters.slice(n_l,n_l+this.remaining_letters.length),
			this.remaining_phonemes.slice(n_p,n_p+this.remaining_phonemes.length),
			cl, cp, this.cum_error
		)
		ps.present_phones = [...this.present_phones,this.remaining_phonemes[0]]
		ps.parent = this
		return ps
	}

	all_next_matching_ps (){
		//If there are no more letters to remove then return itself
		if(this.remaining_phonemes.length == 0 || this.remaining_letters.length == 0){
			return []
		}
		let out = []
		// let did_recurse = false

		//If vowel skipping is enabled then recurse into a branch where
		// //  we skip the vowel if the next phoneme is a vowel
		let next_phoneme1 = this.remaining_phonemes[0]
		// if(!isFirst && allowSkipInteriorVowels &&
		//    this.remaining_phonemes.length != 1 &&
		//    phoneme_types[next_phoneme1]==='vowel'){
		//  	out.push(this.next_ps(0, 1, out, allowSkipInteriorVowels))
		//  	did_recurse = true
		// }

		//Generate Phoneme/Biphoneme objects for the next target phoneme
		let b_p1 = new Biphoneme(next_phoneme1,"")
		let next_phoneme2 = null;
		let b_p2 = null;
		if(this.remaining_phonemes.length > 1){
			next_phoneme2 = this.remaining_phonemes[1]
			b_p2 = new Biphoneme(next_phoneme1, next_phoneme2)
			// console.log(b_p2.toString())
		}

		let possible_graphemes1 = grapheme_dict[b_p1]
		let possible_graphemes2 = grapheme_dict[b_p2]
		let rl = this.remaining_letters.join("")

		// console.log("<<P", possible_graphemes1,possible_graphemes2)
		

		//If the user provided a grapheme (set of letters) that is phonetically
		// correct for the next phoneme then recurse into a brach that commits to that
		if(possible_graphemes1 != null){
			for (let i=0; i < possible_graphemes1.length; i++){
				let g = possible_graphemes1[i];
				// console.log("<<G", g, this.remaining_letters.join(""), this.remaining_letters.join("").startsWith(g))
				if(rl.startsWith(g)){
					out.push(this.next_ps(g.length,1))
					// did_recurse = true
				}
			}
			
		}

		//Same thing but for true Biphonemes like X -> K,S
		if(possible_graphemes2 != null){
			for (let i=0; i < possible_graphemes2.length; i++){
				let g = possible_graphemes2[i];
				if(rl.startsWith(g)){
					out.push(this.next_ps(g.length,2))
					// did_recurse = true
				}
			}
		}

		// //If there isn't a better resolution of the parial spelling
		// //  then just return what we have so far
		// if(out.length > 0){
		// 	return [this]
		// }
		// if(out.length > 0){
		// 	console.log(out)
		// }
		

		return out

	}

	extract_break_down(cull_corrections=false){
		let ps = this
		let out = []
		while(ps.parent != null){
			let pd, pg
			if(cull_corrections && ps.correction){
				if(ps.correction.remove_length > 0){
					pd = [null]
					if(ps.correction.type == 'replace'){
						pg = Array(ps.correction.remove_length).fill(null)
					}else{
						pg = ps.committed_letters.slice(ps.parent.committed_letters.length)
					}
				}else if (ps.correction.type == 'insert'){
					ps = ps.parent
					continue
					// pg = [null]
					// pd = [null]
					
				}
			}else{
				pd = ps.committed_phones.slice(ps.parent.committed_phones.length)
				pg = ps.committed_letters.slice(ps.parent.committed_letters.length)
			}
			if(pg.length == 0){pg = [null]}
			out.splice(0,0,[pd,pg])
			
			
			// console.log(ps.remaining_letters, pd,pg, (ps.correction && ps.correction.type)||null)
			ps = ps.parent
		}

		return out

	}

	find_best_break_down(){
		let head = [this]
		let keep_going = true
		let finals = []
		while(head.length > 0){
			let new_head = []
			for(let ps of head){
				let matches = ps.all_next_matching_ps()
				// console.log("M",matches)
				for(let match of matches ){
					if(match.remaining_phonemes.length > 0){
						new_head.push(match)
					}else{
						finals.push(match)
					}
				}
			}
			head = new_head
		}
		if(finals.length == 0){
			return null
		}
		let ps = finals[0]
		return ps.extract_break_down()
		// let matches = .

	}







	toString(){
	 	let out = []
	 	for(let i=0; i < this.committed_phones.length; i++){
    		out.push(this.committed_phones[i] +":" + this.committed_letters[i]);
    	}
    	let rest = "([" + String(this.remaining_letters) + "],[" + String(this.remaining_phonemes) + "])";
    	return "{" + out.join(",")+ "}" + rest;
	}


	//Helper function used in resolve that makes a new partial spelling and 
	// pushes it to the set of alternative branching phonetic interpretations.
	recurse(n_l, n_p, branches, allowSkipInteriorVowels=false){
		let cl = [...this.committed_letters]
		cl.push(...this.remaining_letters.slice(0,n_l))
		let cp = [...this.committed_phones]
		cp.push(...this.remaining_phonemes.slice(0,n_p))

		let ps = new PartialSpelling(
			this.remaining_letters.slice(n_l,n_l+this.remaining_letters.length),
			this.remaining_phonemes.slice(n_p,n_p+this.remaining_phonemes.length),
			cl, cp
		)
		if(n_l > 0){
			ps.present_phones = [...this.present_phones,...this.remaining_phonemes.slice(0,n_p)]
		}else{
			ps.present_phones = [...this.present_phones]
		}
		
		// console.log("REC", String(ps))

		branches.push(...ps.resolve(allowSkipInteriorVowels,false))
	}


	//Uses dynamic programming to recursively resolve all phonetic 
	//  interpretations of this partial spelling.
	resolve (allowSkipInteriorVowels, isFirst=true){
		//If there are no more letters to remove then return itself
		if(this.remaining_phonemes.length == 0 || this.remaining_letters.length == 0){
			return [this]
		}
		let out = []
		let did_recurse = false

		//If vowel skipping is enabled then recurse into a branch where
		//  we skip the vowel if the next phoneme is a vowel
		let next_phoneme1 = this.remaining_phonemes[0]
		if(!isFirst && allowSkipInteriorVowels &&
		   this.remaining_phonemes.length != 1 &&
		   phoneme_types[next_phoneme1]==='vowel'){
		 	this.recurse(0, 1, out, allowSkipInteriorVowels)
		 	did_recurse = true
		}

		//Generate Phoneme/Biphoneme objects for the next target phoneme
		let b_p1 = new Biphoneme(next_phoneme1,"")
		let next_phoneme2 = null;
		let b_p2 = null;
		if(this.remaining_phonemes.length > 1){
			next_phoneme2 = this.remaining_phonemes[1]
			b_p2 = new Biphoneme(next_phoneme1, next_phoneme2)
		}

		let possible_graphemes1 = grapheme_dict[b_p1]
		let possible_graphemes2 = grapheme_dict[b_p2]

		// console.log("<<P", possible_graphemes1,possible_graphemes2)

		//If the user provided a grapheme (set of letters) that is phonetically
		// correct for the next phoneme then recurse into a brach that commits to that
		if(possible_graphemes1 != null){
			for (let i=0; i < possible_graphemes1.length; i++){
				let g = possible_graphemes1[i];
				// console.log("<<G", g, this.remaining_letters.join(""), this.remaining_letters.join("").startsWith(g))
				if(this.remaining_letters.join("").startsWith(g)){
					this.recurse(g.length,1,out,allowSkipInteriorVowels);
					did_recurse = true
				}
			}
			
		}

		//Same thing but for true Biphonemes like X -> K,S
		if(possible_graphemes2 != null){
			for (let i=0; i < possible_graphemes2.length; i++){
				let g = possible_graphemes2[i];
				if(this.remaining_letters.join("").startsWith(g)){
					this.recurse(g.length,2,out,allowSkipInteriorVowels);
					did_recurse = true
				}
			}
		}

		//If there isn't a better resolution of the parial spelling
		//  then just return what we have so far
		if(!did_recurse){
			return [this]
		}


		return out

	}
}

function apply_correction_sequence(corr_seq,spelling){
	// if(!Array.isArray(word)){word = word.split("")}
	if(!Array.isArray(spelling)){spelling = spelling.split("")}
	for(let corr of corr_seq){
		if(corr.insert_index > spelling.length){
			console.log("error")
		}
		if(corr.insert_value){
			spelling.splice(corr.insert_index,corr.remove_length, corr.insert_value)
		}else{
			spelling.splice(corr.insert_index,corr.remove_length)
		}
	}
	return spelling.join('')

}
function check_correction_sequence(word,spelling,favor_consonant){
	let corr_seq = best_correction_sequence(word,spelling,favor_consonant)
	let corrected = apply_correction_sequence(corr_seq,spelling)
	console.log(word==corrected,word, spelling, "->",corrected)
}

function check_break_down(word){
	let bd = (new PartialSpelling(word,word_phonemes[word],[],[])).find_best_break_down()
	let s = "{"
	for (let [p,d] of bd){
		s += "[" + p.join(",") + "]" + ":" + d.join("") + ", "
	}
	s += "}"
	console.log(s)
}

